.skeleton {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 4px;
}

.skeleton.image {
  width: 100%;
  height: 200px;
}

.skeleton.text {
  height: 1em;
  margin: 0.5em 0;
}

.skeleton.title {
  height: 2em;
  width: 60%;
  margin-bottom: 1em;
}

@keyframes loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
} 